import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './notificationLogIcon.css';

@definition('eui-notification-log-icon', {
  style,
  home: 'notification-log-icon',
  props: {
    notificationCount: { attribute: true, type: Number, default: 0 },
  },
})
export default class NotificationRing extends LitComponent {
  constructor() {
    super();
    this._bindMethods();
  }

  /**
   * @private
   * @description binds the methods
   */
  _bindMethods() {
    this._addNotification = this._addNotification.bind(this);
    this._clearNotification = this._clearNotification.bind(this);
    this._setNotifications = this._setNotifications.bind(this);
    this._updateCounter = this._updateCounter.bind(this);
  }

  /**
   * @protected
   * @description Component did connect to the DOM
   */
  didConnect() {
    document.addEventListener('eui-notification:render', this._addNotification);
    document.addEventListener('eui-notification:set', this._setNotifications);
  }

  /**
   * @protected
   * @description Component will disconnect from the DOM
   */
  didDisconnect() {
    document.removeEventListener('eui-notification:render', this._addNotification);
    document.removeEventListener('eui-notification:set', this._setNotifications);
  }

  /**
   * @protected
   * @description Component did render
   */
  didRender() {
    this._dom = {
      notificationLogTrigger: this.shadowRoot.querySelector('.notification-log-trigger'),
    };
  }

  /**
   * @private
   * @description Handle event
   * @param {Object} event - Click event
   */
  handleEvent(event) {
    if (typeof this.options.onClick === 'function') {
      this.options.onClick(event);
    }
  }

  /**
   * @private
   * @function _updateCounter
   * @description Update counter
   */
  _updateCounter() {
    const hasNotification = 'has-notification';
    const noNotification = 'no-notification';

    if (this.notificationCount === 0) {
      if (this._dom.notificationLogTrigger.classList.contains(hasNotification)) {
        this._dom.notificationLogTrigger.classList.remove(hasNotification);
        this._dom.notificationLogTrigger.classList.add(noNotification);
      }
    } else {
      this.seen = false;
      this._dom.notificationLogTrigger.classList.remove(noNotification);
      this._dom.notificationLogTrigger.classList.add(hasNotification);
    }
  }

  /**
   * @private
   * @function _clearNotification
   * @description Clear notification
   */
  _clearNotification() {
    this.notificationCount -= 1;
    this._updateCounter();
  }

  /**
   * @private
   * @function _addNotification
   * @description Add notification
   */
  _addNotification() {
    this.notificationCount += 1;
    this._updateCounter();
  }

  /**
   * @private
   * @function _setNotifications
   * @description Set notifications
   * @param {Object} event - Click event
   */
  _setNotifications(count) {
    this.notificationCount = count.detail;
    this._updateCounter();
  }

  /**
   * @private
   * @function render
   * @description Render component
   */
  render() {
    return html`
      <div class="notification-log-sign">
        <div
          class="notification-log-trigger no-notification"
          @click=${this}
          data-action="${this.options.action}"
          data-payload="${this.options.payload}"
        >
          <div class="notification-log-number">
            ${this.notificationCount}
          </div>
          <div class="icon-notification">
            <eui-v0-icon name="notification" color="white"></eui-v0-icon>
          </div>
          <div class="icon-notification-ring">
            <eui-v0-icon name="notification-ring" color="white"></eui-v0-icon>
          </div>
        </div>
      </div>
    `;
  }
}
